<template>
  <div :class="$root.themeHomeChange === '1' ? 'ehcartsLine_light' : 'ehcartsLine'">
    
    <div class="header">
      <div class="left">
        <img src="@/assets/img/companyCenter/jiantou.png" />
        <span class="headerTitle">{{title}}</span> 
      </div>
      <el-date-picker
        v-model="date"
        size="mini"
        :type="dateType"
        :format="dateType === 'month' ? 'yyyy 年 MM 月' : 'yyyy 年'"
        :value-format="dateType === 'month' ? 'yyyy-MM' : 'yyyy'"
        placeholder="选择日期"
        style="width: 160px;"
      ></el-date-picker>
    </div>
    
    <div class="content">
      <div id="lineCharts" ref="lineCharts"></div>
    </div>
  </div>
</template>

<script>
  let option = {
    tooltip: {
      show: true,
      trigger: 'axis',
      backgroundColor: 'rgba(42, 92, 176, .6)',
      borderColor: "rgba(0, 255, 239, 1.0)",
      textStyle: {
        color: '#00FFEF'
      },
      axisPointer: {
        show: true,
        type: 'cross',
        label: {}
      }
    },
    toolbox: {
      show: false,
      right: '20px',
      feature: {
        dataView: { show: true, readOnly: true, title: '图表数据' },
        // magicType: { show: true, type: ['line', 'bar'] },
        // restore: { show: true },
        saveAsImage: { show: true, title: '保存为图片'}
      }
    },
    grid: {
      top: '20%',
      bottom: '15%',
      left: '5%',
      right: '5%',
    },
    legend: [],
    xAxis: [
      {
        type: 'category',
        data: ['新塘镇', '新造镇', '石基镇', '钟村镇', '石滩镇', '大岗镇', '狮岭镇', '江高镇', '南村镇', '花东镇', '沙湾镇', '黄埔港', '赤山村', '畲江镇', '石碁镇', '天河村', '石牌镇', '猎德镇', '冼村'],
        axisPointer: {
          type: 'shadow'
        },
        axisLabel: {
          color: '#fff',
          fontWeight: 'bold',
          interval: 0,
        }
      }
    ],
    yAxis: [],
    series: [
      {
        name: '增收',
        type: 'line',
        showSymbol: true,
        // symbol: 'circle',     //折点设定为实心点
        symbolSize: 6,   //设定实心点的大小
        itemStyle: {
            color: '#00DDFF',
            shadowColor: '#00DDFF',
            shadowBlur: 20,
        },
        lineStyle: {
          shadowColor: "#00DDFF",
          shadowOffsetX: 0,
          shadowOffsetY: 0,
          opacity: 1,
          shadowBlur: 8,
          type: "solid",
          width: 3
        },
        data: [78, 59, 39, 60, 87, 46, 75, 77, 36, 85, 69, 38, 67, 36, 85, 89, 58, 47, 76]
      },
      {
        name: '节支',
        type: 'line',
        showSymbol: true,
        // symbol: 'circle',     //折点设定为实心点
        symbolSize: 6,   //设定实心点的大小
        yAxisIndex: 1,
        itemStyle: {
            color: '#fdea73',
            shadowColor: '#fdea73',
            shadowBlur: 20,
        },
        lineStyle: {
          shadowColor: "#fdea73",
          shadowOffsetX: 0,
          shadowOffsetY: 0,
          opacity: 1,
          shadowBlur: 8,
          type: "solid",
          width: 3
        },
        data: [60, 54, 79, 60, 37, 76, 75, 37, 56, 85, 69, 78, 57, 76, 85, 69, 78, 57, 40]
      }
    ]
  };
  import * as echarts from "echarts";
  import bus from '@/utils/bus';
  export default {
    name: "ehcartsLine",
    props: {
      title: {
        type: String,
        default: () => {
          return '智能组件';
        },
      },
      dateType: {
        type: String,
        default: () => {
          return 'year';
        },
      },
      data: {
        type: Array,
        default: () => {
          return [];
        },
      }
    },
    data(){
      return {
        time: null,
        date: '',
        mychart: '',
        outSideOption: {},
        option: {}
      }
    },
    mounted() {
      this.time = setInterval(()=>{
        this.mychart.resize()
      },300);
      // this.init();
    },
    created() {},
    watch: {
      '$root.themeHomeChange'() {
        this.init(this.outSideOption);
      }
    },
    methods: {
      dataSet(outSideOption = {}) {
        this.option = JSON.parse(JSON.stringify(option))
        // 初始化时间选择框数据
        let nowDate = new Date();
        let year = nowDate.getFullYear(); 
        let month = nowDate.getMonth()+1;
        month = (month < 10 ? "0" + month : month); 
        this.date = year + '-' + month;
        
        // window.console.log(outSideOption)
        
        
        // 组件外配置项注入
        if(outSideOption.isUseOriginal) {
          this.option.legend.forEach((item, index) => {
            if(outSideOption.isShowLegend) {
              // window.console.log(item, 'item', outSideOption.series.name[index], item.data[0].name)
              item.data[0].name = outSideOption.series.name[index];
            } else {
              item.show = outSideOption.isShowLegend;
            }
          });
          // this.option.tooltip.formatter  = `
          //   {b0}${outSideOption.xAxis.additionalText} <br />
          //   ●{a0}：{c0} <br />
          //   ●{a1}：{c1}`;
          this.option.tooltip.axisPointer.label.formatter =  params => {
            // window.console.log(params);
            return params.axisDimension == 'x' ? params.value + '供电分局' : params.value.toFixed(2);
          }
        } else {
          this.option.legend = outSideOption.legend;
          this.option.legend.forEach((item, index) => {
            if(outSideOption.isShowLegend) {
              // window.console.log(item, 'item', outSideOption.series.map((item) => { return item.name }))
              item.data[0].name = outSideOption.series.map((item) => { return item.name })[index];
            } else {
              item.show = outSideOption.isShowLegend;
            }
          });
        }
        if(outSideOption.xAxis.additionalText) {
          this.option.tooltip.formatter  = (params) => {
            // window.console.log(params)
            let html = [
              `<p style="padding-bottom: 4px; margin-bottom: 4px;">`,
              `<strong>${params[0].name} ${outSideOption.xAxis.additionalText}</strong>`,
              `</p>`,
              `<p style="text-align: left;">`,
              `<span style="color: ${params[0].color};">● </span>`,
              `${params[0].seriesName}：<strong>${params[0].value}</strong>`,
              `</p>`,
              `<p style="text-align: left;">`,
              `<span style="color: ${params[1].color};">● </span>`,
              `${params[1].seriesName}：<strong>${params[1].value}</strong>`,
              `</p>`
            ];
            return html.join("");
          };
        }
        
        this.option.xAxis[0].data = outSideOption.xAxis.data;
        this.option.yAxis = outSideOption.yAxis;
        
        if(outSideOption.isUseOriginal) {
          this.option.series.forEach((item, index) => {
            item.data = outSideOption.series.data[index];
            item.name = outSideOption.series.name[index];
          });
        } else {
          this.option.series = outSideOption.series;
        }
        
        this.setTheme();
      },
      setTheme(){
        // window.console.log(23123);
        //样式区分
        if (this.$root.themeHomeChange === '1'){  //深色背景
          this.option.legend.forEach(item => {
            item.textStyle = {color: '#333'}
          })
          // this.option.legend.textStyle = {color: '#333333'}
          this.option.xAxis[0].axisLabel.textStyle = {
            color: "#333333",
            margin: 15,
            fontWeight: 'bold',
          }
          this.option.yAxis.forEach(item => {
            item.axisLabel.textStyle = {
              color: "#333333",
              margin: 15,
              // fontWeight: 'bold',
            }
          })
        }else if(this.$root.themeHomeChange==='2'){ //浅色背景
          this.option.legend.forEach(item => {
            item.textStyle = {color: '#ffffff'}
          })
          // this.option.legend.textStyle = {color: '#ffffff'};
          this.option.xAxis[0].axisLabel.textStyle = {
            color: "#ffffff",
            margin: 15,
            fontWeight: 'bold',
          }
          this.option.yAxis.forEach(item => {
            item.axisLabel.textStyle = {
              color: "#66B9FF",
              margin: 15,
              // fontWeight: 'bold',
            }
          })
        }
      },
      init(outSideOption = {}) {
        this.outSideOption = JSON.parse(JSON.stringify(outSideOption));
        this.dataSet(outSideOption);
        
        this.mychart = echarts.init(this.$refs.lineCharts);
        
        this.mychart.setOption(this.option);
        
        window.addEventListener("resize", ()=>{
          this.mychart.resize()
        });
      }
    },
    beforeDestroy() {
      bus.$off('isCollapse');
      clearInterval(this.time);
      window.removeEventListener("resize", () => {
        this.mychart.resize();
      });
    },
  }
</script>

<style scoped lang="scss">
  .ehcartsLine{
    width: calc(100% - 20px);
    display: flex;
    flex-wrap: wrap;
    height: calc((100% - 125px) / 3);
    padding: 10px;
    margin-top: 15px;
    background: url(../../assets/img/riskControlCenter/jhxhgrw.png) no-repeat;
    background-size: 100% 100%;
    /deep/ .el-date-editor .el-input__inner {
      background-color: #041338;
      color: #d3d3d3;
      border: 1px solid #2a5cb0;
      border-radius: 3px;
      text-align: center;
    }
    .header {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 5px;
      height: 32px;
      .left {
        display: flex;
        align-items: center;
        // padding-left: 10px;
        .headerTitle {
          font-family: MicrosoftYaHei-Bold;
          font-size: 17px;
          color: #00F8FF;
          font-weight: 600;
          margin-left: 5px;
        }
      }
    }
    .content {
      width: 100%;
      height: calc(100% - 42px);
      display: flex;
      div {
        width: 100%;
        height: 100%;
        position: relative;
      }
    }
  }
  
  // 浅色主题
  .ehcartsLine_light {
    width: calc(100% - 20px);
    display: flex;
    flex-wrap: wrap;
    height: calc((100% - 125px) / 3);
    padding: 10px;
    margin-top: 15px;
    // background: url(../../assets/img/riskControlCenter/jhxhgrw.png) no-repeat;
    // background-size: 100% 100%;
    background: #fff;
    box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
    border-radius: 6px;
    background-size: 100% 100%;
    position: relative;
    overflow: hidden;
    &::after {
      content: "";   
      display: block;
      width: 50px;   
      height: 5px;
      background: #4085fa;
      position: absolute;
      top: -5px;
      left: -15px;
      transform: rotate(-45deg);
      // border-radius: 50px 0;
    }
    &::before {
      content: "";   
      display: block;
      width: 50px;   
      height: 5px;
      background: #4085fa;
      position: absolute;
      bottom: -5px;
      right: -15px;
      transform: rotate(-45deg);
      // border-radius: 50px 0;
    }
    /deep/ .el-date-editor .el-input__inner {
      background-color: #fff;
      color: #333;
      border: 1px solid #bbb;
      border-radius: 3px;
      text-align: center;
    }
    .header {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 5px;
      height: 32px;
      .left {
        display: flex;
        align-items: center;
        // padding-left: 10px;
        .headerTitle {
          font-family: MicrosoftYaHei-Bold;
          font-size: 17px;
          color: #4389F9;
          font-weight: 600;
          margin-left: 5px;
        }
      }
    }
    .content {
      width: 100%;
      height: calc(100% - 42px);
      display: flex;
      div {
        width: 100%;
        height: 100%;
        position: relative;
      }
    }
  }
</style>